@import "reset";

body {
  background: #7C0117;
  font-family: 'Space Mono', monospace;
  font-weight: 400;
  line-height: 1.3;
  color: #F0D3D3;
  text-align: center;
}
p {margin-bottom: 1rem;}

h1, h2, h3 {
  margin: 3rem 0 1.38rem;
  font-family: 'BioRhyme', serif;
  font-weight: 400;
  line-height: 1.3;
}
p {margin-bottom: 1rem;}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
.avatar img {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}
div.a {
  font-size: 20px;
}
a:link, a:visited, a:hover, a:active {
color:whitesmoke;
padding: 1px 5px;
text-align: center;
text-decoration: underline;
display: inline-block;
}

nav {
  border-bottom: 2px solid whitesmoke;
  padding: 1rem 0;
  margin-bottom: 2rem;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-evenly;
}

nav li {
  text-align: center;
  text-transform: uppercase;
}
@media screen and (min-width: 600px) {
  body {font-size: 1.3rem}

  .about, .work {
    grid-gap: 3rem;
  }

  nav { margin-bottom: 3rem; }

    img {
      position: relative;
      top: -40%;
      height: auto;
      width: auto; 
    }
  }
@media screen and (max-width: 599px) {
  h1 {font-size: 2.3rem;}
  img {
    height: auto;
    width: auto;
  }
}

@media screen and (max-width: 374px) {
  p {font-size: .90rem;}
  img {
    height: auto;
    width: auto;
  }
}
@media screen and (min-width: 1200px) {
  nav {margin-bottom: 6rem;}
img {
    height: auto;
    width: auto;
    position: static
  }
  footer {margin-top: 6rem;}

  .avatar { 
    height: auto;
    grid-area: portrait;
    }
  }
   .work {
    display: grid;
    grid-template-columns: auto auto;
  }
  